import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController, Loading, Platform } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { ToolsProvider } from '../../providers/tools/tools';
import { DomSanitizer } from '@angular/platform-browser';
import { RoomsServiceProvider } from '../../providers/rooms-service/rooms-service';
import { CommonServiceProvider } from '../../providers/common-service/common-service';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { NotificationProvider } from '../../providers/notification/notification';

/**
 * Generated class for the RoomServiceBookingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-room-service-booking',
  templateUrl: 'room-service-booking.html',
})
export class RoomServiceBookingPage {
  roomServiceBookingForm: FormGroup;
  loading: Loading;
  descriptionHtml: any;
  backgroundImage: any; // to store background image url
  timeSlots: any = [];
  monthNames: any; // for month names
  monthShortNames: any = []; // for month short names
  minDate: any = new Date();
  maxDate: any;
  itemList: any;
  platFormIsIOS: boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, public formBuilder: FormBuilder,
    public toolsPvdr: ToolsProvider, public alertCtrl: AlertController, public sanitized: DomSanitizer,
    private roomsProvider: RoomsServiceProvider, private commonProvider: CommonServiceProvider, private reservationProvider: ReservationServiceProvider, private notificationProvider: NotificationProvider, private loadingCtrl: LoadingController, private platForm: Platform) {

    this.roomServiceBookingForm = this.formBuilder.group({
      'comments': new FormControl(null, [Validators.required]),
      'date': new FormControl(null, [Validators.required]),
      'time': new FormControl('', [Validators.required])
    });

    // translate month names
    let monthList = this.toolsPvdr.fnLanguageTranslate("common.monthNames");
    monthList.subscribe((value: string) => {
      this.monthNames = value;
    });
    //translated month short names
    for (let i = 0; i < 12; i++) {
      this.monthShortNames.push(this.toolsPvdr.getShortMonthName(i));
    }

    if (this.platForm.is("ios")) {
      this.platFormIsIOS = true;
    }

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RoomServiceBookingPage');
    console.log(this.roomsProvider.objRoomService);
    this.itemList = this.roomsProvider.objRoomService.currentOrderList;
    for (let item of this.itemList) {
      if (item.isSelected == true) {
        this.backgroundImage = item.imgPath;
        break;
      }
    }

    this.maxDate = this.toolsPvdr.fnDateFormatChange(this.commonProvider.calculateMaxDate());
    this.minDate = this.toolsPvdr.fnGetToday();
    let defaultTextTranslate = this.toolsPvdr.fnLanguageTranslate("view.reservation.label.roomservice"); // language translation

    let defaultText = '';
    defaultTextTranslate.subscribe((value: string) => {
      defaultText = value;
    });
    // bypass HTML tags 
    this.descriptionHtml = this.sanitized.bypassSecurityTrustHtml(defaultText);

    //inserts all time intervals into array timeSlots
    this.timeSlots = this.roomsProvider.fnGetTimeSlotsForRoomService();

    //to set start date based on time slots available
    let lastTimeInterval = this.timeSlots.length > 0 ? this.timeSlots[this.timeSlots.length - 1] : "0:00";
    this.minDate = this.toolsPvdr.fnSetStartDate(lastTimeInterval);

    this.fnCheckPreEnteredFormData();
  }

  // fetch pre-entered data into fields from model
  fnCheckPreEnteredFormData() {
    if (this.commonProvider.objFormData && this.commonProvider.objFormData.roomservice) {
      this.commonProvider.objFormData.roomservice.bookingCompleted = false;
      this.roomServiceBookingForm.get("date").setValue(this.commonProvider.objFormData.roomservice.date);
      this.roomServiceBookingForm.controls['time'] && this.roomServiceBookingForm.get("time").setValue(this.commonProvider.objFormData.roomservice.time);
      this.roomServiceBookingForm.get("comments").setValue(this.commonProvider.objFormData.roomservice.comments);
    }
  }

  // store entered data into model before leaving page
  public ionViewWillLeave() {
    if (this.commonProvider.objFormData.roomservice.bookingCompleted != true) {
      this.commonProvider.objFormData.roomservice.date = this.roomServiceBookingForm.get('date').value;
      this.commonProvider.objFormData.roomservice.time = this.roomServiceBookingForm.controls['time'] && this.roomServiceBookingForm.get('time').value;
      this.commonProvider.objFormData.roomservice.comments = this.roomServiceBookingForm.get('comments').value;
    }
  }

  public fnDateChange() {

    let date = this.toolsPvdr.fnDateFormatChange(this.roomServiceBookingForm.controls['date'] && new Date(this.roomServiceBookingForm.get('date').value));
    let isToday: boolean;
    let today = this.toolsPvdr.fnGetToday();
    if (date == today) {
      isToday = true;
    } else {
      isToday = false;
    }

    //inserts all time intervals into array timeSlots
    this.timeSlots = this.roomsProvider.fnGetTimeSlotsForRoomService(isToday);
  }

  //function to go to previous page
  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  fnSubmit() {
    // if already check-out completed 
    if (this.reservationProvider.objReservation.alreadyVerifiedCheckOut) {
      let checkinMsg = this.toolsPvdr.fnLanguageTranslate(['common.error', "view.reservation.error.checkoutYaRealizado", "buttons.ok"]);
      checkinMsg.subscribe((value: string) => {
        let alertCheckin = this.alertCtrl.create({
          title: value['common.error'],
          message: value['view.reservation.error.checkoutYaRealizado'],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel',
              handler: data => {
              }
            }]
        });
        alertCheckin.present();
        setTimeout(() => { alertCheckin.dismiss(); }, 5000);
      });
      return;
    }
    else {
      if (this.roomServiceBookingForm.controls['date'].hasError('required')) {
        let validateMessages = this.toolsPvdr.fnLanguageTranslate(['view.reservation.error.nullDate', "common.error", "buttons.ok"]);

        validateMessages.subscribe((value: string) => {
          let alertnew = this.alertCtrl.create({
            title: value['common.error'],
            message: value['view.reservation.error.nullDate'],
            cssClass: 'cls_alrt',
            buttons: [
              {
                text: value['buttons.ok'],
                role: 'cancel'
              }]
          });
          alertnew.present();
          setTimeout(() => { alertnew.dismiss(); }, 5000);
        });
      } else if (this.reservationProvider.objReservation.alreadyVerifiedCheckOut) {

        let cantBookMsg = this.toolsPvdr.fnLanguageTranslate(['view.reservation.error.checkoutYaRealizado', "common.error", "buttons.ok"]);

        cantBookMsg.subscribe((value: string) => {
          let alertbook = this.alertCtrl.create({
            title: value['common.error'],
            message: value['view.reservation.error.checkoutYaRealizado'],
            cssClass: 'cls_alrt',
            buttons: [
              {
                text: value['buttons.ok'],
                role: 'cancel'
              }]
          });
          alertbook.present();
          setTimeout(() => { alertbook.dismiss(); }, 5000);
        });

      } else {
        let confirmMessages = this.toolsPvdr.fnLanguageTranslate(['buttons.send', 'buttons.modify', 'view.reservation.confirm', 'common.confirm']); //for language translation

        confirmMessages.subscribe((value: string) => {
          let alertnew = this.alertCtrl.create({
            title: value['common.confirm'],
            message: value['view.reservation.confirm'],
            cssClass: 'cls_alrt',
            buttons: [
              {
                text: value['buttons.modify'],
                role: 'cancel',
                handler: data => {

                }
              },
              {
                text: value['buttons.send'],
                handler: data => {
                  //date format changes for nostay user
                  this.showLoading();
                  let bookingDate: any;
                  let time = this.roomServiceBookingForm.controls['time'] && this.roomServiceBookingForm.get('time').value ? this.roomServiceBookingForm.get('time').value + ':00' : '00:00:00';

                  bookingDate = this.toolsPvdr.dateFormatChangeForService(this.roomServiceBookingForm.controls['date'] && new Date(this.roomServiceBookingForm.get('date').value)).split(" ")[0] + ' ' + time;

                  let date = bookingDate;
                  let comments = this.roomServiceBookingForm.get('comments').value;

                  this.roomsProvider.sendRoomServiceRequest(date, comments).subscribe(data => {
                    this.loading.dismiss();
                    if (data.success == true) {
                      this.roomServiceBookingForm.reset();
                      this.commonProvider.objFormData.restaurant = [];
                      this.commonProvider.objFormData.roomservice.bookingCompleted = true;
                      //set selected quantity as 0
                      for (var i = 0; i < this.roomsProvider.objRoomService.currentOrderList.length
                        ; i++) {
                        for (var j = 0; j < this.roomsProvider.objRoomService.currentOrderList[i].availableItems.length; j++) {
                          this.roomsProvider.objRoomService.currentOrderList[i].availableItems[j].quantity = 0;
                          this.roomsProvider.objRoomService.currentOrderList[i].isSelected = false;
                        }
                      }
                      for (var i = 0; i < this.roomsProvider.objRoomService.serviceItemsList.length; i++) {
                        this.roomsProvider.objRoomService.serviceItemsList[i].quantity = 0;
                      }
                      this.roomsProvider.objRoomService.cartTotal = 0;
                      // success message translate
                      let successMessages = this.toolsPvdr.fnLanguageTranslate(['view.reservation.success', "common.requestReceived", "buttons.ok"]);

                      successMessages.subscribe((value: string) => {
                        let alertsuccess = this.alertCtrl.create({
                          title: value['common.requestReceived'],
                          message: value['view.reservation.success'],
                          cssClass: 'cls_alrt',
                          buttons: [
                            {
                              text: value['buttons.ok'],
                              role: 'cancel',
                              handler: data => {
                                this.navCtrl.popTo(this.navCtrl.getByIndex(1), { animate: true, animation: 'transition', duration: 500, direction: 'back' });
                              }
                            }]
                        });
                        alertsuccess.present();
                        setTimeout(() => {
                          alertsuccess.dismiss();
                          this.navCtrl.popTo(this.navCtrl.getByIndex(1), { animate: true, animation: 'transition', duration: 500, direction: 'back' });
                        }, 5000);
                      });
                    }
                  });

                }
              }]
          });
          alertnew.present();
          setTimeout(() => { alertnew.dismiss(); }, 5000);
        });

      }
    }
  }
  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }
}



